<template>
  <div class="xzxc-menu">
  	<br>
    <el-form v-if="admin" :label-position="this.$store.state.panelLayout" label-width="100px">
      <el-row :gutter="24">
        <el-col :lg="8">
          <el-form-item label="选择用户：">
            <get-user-data :getUserIdFn="getSetUser"></get-user-data> 
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  	<el-table
      :data="imgLibsTableData"
      style="width: 100%" stripe border height="500">
      <el-table-column
        prop="img_path"
        label="图片"
        width="180">
        <template slot-scope="scope">
            <img style="width:130px;border-radius:5px;" :src="imgpath + '/' + scope.row.img_path">
        </template>
      </el-table-column>
      <el-table-column
        prop="timestr"
        label="添加时间"
        width="180">
      </el-table-column>
      <el-table-column
        prop="isDelete"
        label="状态">
        <template slot-scope="scope">
            <el-tag size="mini" type="danger" v-if="scope.row.isDelete == 0">图片已禁用</el-tag>
            <el-tag size="mini" type="success" v-else>正常</el-tag>
        </template>
      </el-table-column>

      <el-table-column
        label="操作"
        width="180" v-if="admin">
        <template slot-scope="scope">
          <el-button size="small" type="success" icon="el-icon-refresh" v-if="scope.row.isDelete == 0" @click="deleteImg(scope.row,'show')">启用</el-button>
          <el-button size="small" type="danger" icon="el-icon-delete" v-else @click="deleteImg(scope.row,'delete')">禁用</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="paging-box">
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="pager.currentPage3"
          :page-size="pager.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="pager.count">
        </el-pagination>
      </div>
  </div>
</template>

<script>
import GetUserData from './getUserData'
export default {
  components: {
    GetUserData,
  },
  data () {
    return {
      userid: 0,
      userInfo: null,
      adminlevel: 0,
      admin: false,
      imgLibsTableData: null,
      imgpath: this.$store.state.staticPath,
      pager: {
        currentPage3: 1,
        count: 0,
        pageSize: 10,
        size: 0,
        user_name: ''
      }
    }
  },
  created () {
    var _this = this
    _this.userInfo = _this.$store.state.userInfo
    _this.adminlevel =  _this.userInfo ? _this.userInfo.level : 0
    if (_this.userInfo) {
      if (_this.userInfo.level === 1) {
        _this.admin = true
      }
    }
    _this.getImgLibsData()
  },
  methods: {
    handleSizeChange (val) {
      this.pager.pageSize = val
      this.getImgLibsData()
    },
    handleCurrentChange (val) {
      this.pager.size = val - 1
      this.getImgLibsData()
    },
    getSetUser (user) {
        this.userid = user.id
        this.pager.size = 0
        this.pager.pageSize = 10
        this.pager.count = 0
        this.getImgLibsData()
    },
    deleteImg (item,type) {
      var _this = this
      this.$axios({
           type: '$axios',
           obj: {
            url: _this.api.deleteImgLib,
            method: 'post',
            success (res) {
              if (res.status == 200) {
                _this.alertsu('用户禁用成功！')
                _this.getImgLibsData()
              } else {
                _this.alertsu('用户禁用失败！')
              }
            },
            error () {
              _this.alertsu('用户禁用失败！')
            },
            data: {
              id: item.id,
              type: type
            },
            header: {
              'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
              'content-type': 'application/json; charset=utf-8'
            }
          }
      })
    },
  	getImgLibsData () {
      var _this = this
      _this.$get({
      type: '$get',
      obj: {
        url: _this.api.getlibsimg,
        success: function (res) {
          if (res.data.code == 200) {
            console.log(res)
            _this.imgLibsTableData = res.data.data.imgdata
            _this.pager.count = res.data.data.count
          }
          else {
            _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
          }

        },
        error: function () {
          _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
        }
      },
      param: {
        userid: this.userid,
        size: _this.pager.size,
        sizes: _this.pager.pageSize,
        adminlevel: this.adminlevel
      }
    })
    }
  }
}
</script>
<style lang="less">

</style>



